import "./Navigation.css";
import Logo from "./logo.png";
import LogoText from "./Tanisha logo text.jpg";
import Customer from "./customer.gif";
import Document from "./document.gif";
import Social from "./social-media.gif";
import Wave from "./wave.gif";
import { Link } from 'react-router-dom';
import React from 'react';
import { useLocation } from 'react-router-dom';
import Menu from './menu.gif';
import Close from './left-arrow.gif'

function Navigation() {

    function openNav() {
        document.getElementById("overlay").style.width = "4cm";
    }

    function closeNav() {
        document.getElementById("overlay").style.width = "0cm";
    }


    const location = useLocation();

    const isActive = (path) => location.pathname === path;
    return (
        <div className="Navigation">
            <div >

                <Link className="logo-container" to="/">
                    <img className="logo" src={Logo} alt="logo"></img>
                    <img className="logotext" src={LogoText} alt="logo with text"></img>
                </Link>
            </div>
            <img className="menu-icon" id="menu-icon" src={Menu} onClick={openNav} ></img>
            <div className="menu-container">

                <div className="menu-items ">
                    <img src={Wave} alt="Wave for About" className="About"></img>

                    <Link to="/about" className={isActive('/about') ? ' item active' : 'item'}>About</Link>
                </div>
                <div className="menu-items">
                    <img src={Document} alt="Document for Projects" className="Projects"></img>
                    <Link to="/projects" className={isActive('/projects') ? ' item active' : 'item'}>Projects
                    </Link>
                </div>
                <div className="menu-items">
                    <img src={Customer} alt="Customer for Community" className="Community"></img>
                    <Link to="/community" className={isActive('/community') ? ' item active' : 'item'}>Community
                    </Link>
                </div>
                <div className="menu-items">
                    <img src={Social} alt="Social for Contact" className="Contact"></img>
                    <Link to="/contact" className={isActive('/contact') ? ' item active' : 'item'}>Contact 
                    </Link>
                </div>
            </div>
            <div className="overlay" id="overlay">
                <img className="cls-btn" src={Close} id="cls-btn" onClick={closeNav}></img>
                <div className="overlayItems">
                    {/* <h4 className="item">Home</h4>
                    <h4 className="item">About Us</h4>
                    <h4 className="item">Career</h4>
                    <h4 className="item">Contact Us</h4> */}
                    <div className="menu-items ">
                        <img src={Wave} alt="Wave for About" className="About"></img>
                        <Link to="/about" className={isActive('/about') ? ' item active' : 'item'}>About</Link>
                    </div>
                    <div className="menu-items">
                        <img src={Document} alt="Document for Projects" className="Projects"></img>
                        <Link to="/projects" className={isActive('/projects') ? ' item active' : 'item'}>Projects</Link>
                    </div>
                    <div className="menu-items">
                        <img src={Customer} alt="Customer for Community" className="Community"></img>
                        <Link to="/community" className={isActive('/community') ? ' item active' : 'item'}>Community</Link>
                    </div>
                    <div className="menu-items">
                        <img src={Social} alt="Social for Contact" className="Contact"></img>
                        <Link to="/contact" className={isActive('/contact') ? ' item active' : 'item'}>Contact</Link>
                    </div>
                </div>
            </div>
        </div>

    )

}

export default Navigation;